import { NgModule, ApplicationRef } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';
import { HighlightJsModule, HighlightJsService } from 'angular2-highlight-js';

import { NgSelectModule, NgColorModule, NgPageModule } from '../lib';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';

import { NgSelectMainComponent } from './ng-select/main.component';
import { NgColorMainComponent } from './ng-color/main.component';
import { NgPageMainComponent } from './ng-page/main.component';


import { ApiService } from './shared';
import { routing } from './app.routing';

import { removeNgStyles, createNewHosts } from '@angularclass/hmr';

@NgModule({
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    HighlightJsModule,

    routing,
    NgColorModule,
    NgSelectModule,
    NgPageModule,
  ],
  declarations: [
    AppComponent,
    HomeComponent,

    NgSelectMainComponent,
    NgColorMainComponent,
    NgPageMainComponent,
  ],
  providers: [
    HighlightJsService,
    ApiService
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
  constructor(public appRef: ApplicationRef) {}
  hmrOnInit(store) {
    console.log('HMR store', store);
  }
  hmrOnDestroy(store) {
    let cmpLocation = this.appRef.components.map(cmp => cmp.location.nativeElement);
    // recreate elements
    store.disposeOldHosts = createNewHosts(cmpLocation);
    // remove styles
    removeNgStyles();
  }
  hmrAfterDestroy(store) {
    // display new elements
    store.disposeOldHosts();
    delete store.disposeOldHosts;
  }
}



